import React from 'react';
import Warper from './Warper';
import Popup from "reactjs-popup";

const contentStyle = {
  maxWidth: '1200px',
  width: '90%',
};

const CustomModal = () => (
  <Popup
    trigger={
      <button type="button" className="button">
        {' '}
        Click to View Instructions{' '}
      </button>
    }
    modal
    lockScroll={false}
    contentStyle={contentStyle}>
    {close => (
      <div className="modal">
        <a className="close" onClick={close}>
          &times;
        </a>
        <div className="header"> Instruction </div>
        <div className="content">
          {' '}
          <div class="col-12" id="actualInstructions">
              <p class="d-block col-12 text-center"><em>Firstly, thanks for participating in our data collection!</em>
               <br /> <strong>Please Read instructions below.</strong>
              </p>

              <ol>
                  <li class="list-item">
                      You are given a 5 minutes long transcript and a corresponding video.
                  </li>
                  <li class="list-item">
                      Please <strong>Read</strong> the whole transcripts and <strong>Choose</strong> topic sentences that represent the main idea of the transcript by checking checkboxes next to each sentence.
                  </li>
                  <li class="list-item">
                      Then, <strong>Write</strong> a short summary (1~2 sentences recommended) in the summary input box based on your selected sentences.
                  </li>
                  <li class="list-item">
                      You should select <strong>at least ONE</strong> sentence from the transcript but <strong>choosing 3 to 7 sentences</strong> (or more if necessary) is recommended.
                  </li>
                  <li class="list-item">
                      The selected sentences should summarize the 5 minute video clip and be closely related to your written summary.
                  </li>
                  <li class="list-item">
                      The video lasts more than 5 minutes. You should do your task based on the shown transcript and the corresponding video content.
                  </li>
                  <li class="list-item">
                      You do NOT need to watch the entire 5 minute long video if you can do your task with the given transcript.
                  </li>
              </ol>

              Video Player Control
              <ul>
                  <li class="list-item">
                      By pressing each timestamp, you can jump to that exact time in the video, which is provided to help you understand the context.
                  </li>
                  <li class="list-item">
                      Clicking the Video Player area enables play or pause the video.
                  </li>
                  <li class="list-item">
                    The title of the video is shown on the top of the transcript.
                  </li>
              </ul>
          </div>
        </div>

      </div>
    )}
  </Popup>
);

export default Warper(CustomModal);
